<template>
    <div class="screen-box">
        <div class="container">

            <!-- 图片 -->
            <div v-show="iden === 1">
                <div class="screen-block">
                    <h2 class="screen-title">分类：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in classify" :key="index" ref="classify" @click="addActiveClass($refs.classify, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">尺寸（宽度）：</h2>
                    <ul class="screen-list screen-size">
                        <li class="screen-item" v-for="(item, index) in sizes" :key="index" ref="sizes" @click="addActiveClass($refs.sizes, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title screen-title-color">主体颜色：</h2>
                    <ul class="screen-list screen-color">
                        <li class="screen-item-color" v-for="(item, index) in screenColors" :class="item" :key="index" ref="colorsList" @click="addActiveClass($refs.colorsList, index)"><span></span></li>
                    </ul>
                </div>
            </div>

            <!-- 视频 -->
            <div v-show="iden === 2">
                <div class="screen-block">
                    <h2 class="screen-title">分类：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in classify" :key="index" ref="classify" @click="addActiveClass($refs.classify, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">时长：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in times" :key="index" ref="times" @click="addActiveClass($refs.times, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">大小：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in memory" :key="index" ref="memory" @click="addActiveClass($refs.memory, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">规格：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in specs" :key="index" ref="specs" @click="addActiveClass($refs.specs, index)">{{ item }}</li>
                    </ul>
                </div>
            </div>

            <!-- 音频 -->
            <div v-show="iden === 3">
                <div class="screen-block">
                    <h2 class="screen-title">分类：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in audioClass" :key="index" ref="audioClass" @click="addActiveClass($refs.audioClass, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">情感：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in emotion" :key="index" ref="emotion" @click="addActiveClass($refs.emotion, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">乐器：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in instruments" :key="index" ref="instruments" @click="addActiveClass($refs.instruments, index)">{{ item }}</li>
                    </ul>
                </div>
                <div class="screen-block">
                    <h2 class="screen-title">时长：</h2>
                    <ul class="screen-list">
                        <li class="screen-item" v-for="(item, index) in audioTimes" :key="index" ref="audioTimes" @click="addActiveClass($refs.audioTimes, index)">{{ item }}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                classify: ["不限", "人物", "风景", "历史", "科技", "情感", "动物", "植物"], // 分类
                screenColors: [ "multicolour", "red", "orange", "yellow", "green", "cyan", "blue", "purple", "white", "black" ], // 颜色分类
                sizes: ["不限", "100～300px", "300～800px", "800～1400px", "1400～2000px", "2000px以上"], // 尺寸分类
                times: ["不限", "5分钟以内", "5~15分钟", "15~30分钟", "30分钟以上"], // 时长
                memory: ["不限", "100Mb以内", "100~500Mb", "500~1000Mb", "1000Mb以上"], // 内存大小
                specs: ["不限", "宽屏", "竖屏"], // 规格
                emotion: ["不限", "欢快", "舒适", "煽情", "悲伤", "激情", "恐怖", "神秘", "空灵"], // 情感
                instruments: ["不限", "钢琴", "打击乐", "手风琴", "小提琴"], // 乐器
                audioTimes: ["不限", "2分钟以内", "2~5分钟", "5~10分钟", "10分钟以上"], // 音频时长
                audioClass: ["不限", "音乐", "场景", "音效"], // 音频分类
            }
        },
        created () {
        },
        methods: {

            // 增加选中状态
            addActiveClass (list, index) {
                const colorsList = list;
                colorsList.forEach((item, i) => {
                    item.classList.remove("active");
                    if (i === index) {
                        item.classList.add("active");
                    }
                    return true;
                })
            }
        },
        props: ["iden"]
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>
    
    .screen-box {
        background: #F5F5F5;
        margin-bottom: 6px;
        overflow: hidden;
        .container {
            width: 90%;
            max-width: 1000px;
            margin: 25px auto 20px;
            .screen-block {
                margin-bottom: 12px;
                position: relative;
                .screen-title {
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                .screen-title-color {
                    line-height: 26px;
                }
                .screen-list {
                    padding-left: 52px;
                    overflow: hidden;
                    .screen-item {
                        padding: 1px 10px;
                        text-align: center;
                        border-radius: 20px;
                        margin: {
                            right: 10px;
                            bottom: 5px;
                        }
                        float: left;
                        cursor: pointer;
                    }
                    .active {
                        color: #fff;
                        background-color: #48ABFF;
                    }
                }

                /*颜色筛选*/
                .screen-color {
                    padding-left: 90px;
                    .multicolour {
                        span {
                            background: url(../../../static/images_icon/sprite.png) no-repeat;
                            background-position: -282px -246px;
                            background-size: auto 300px;
                        }
                    }
                    .screen-item-color {
                        padding: 1px;
                        margin: {
                            right: 10px;
                            bottom: 5px;
                        }
                        float: left;
                        cursor: pointer;
                        border: 2px solid #F5F5F5;
                        span {
                            width: 20px;
                            height: 20px;
                        }
                    }
                    .active {
                        border: 2px solid #48ABFF;
                    }
                }
                .screen-size {
                    padding-left: 110px;
                }
            }
        }
    }

    // 颜色筛选
    .red span {
        background-color: red;
    }

    .orange span {
        background-color: orange;
    }

    .yellow span {
        background-color: yellow;
    }

    .green span {
        background-color: green;
    }

    .cyan span {
        background-color: cyan;
    }

    .blue span {
        background-color: blue;
    }

    .purple span {
        background-color: purple;
    }

    .black span {
        background-color: black;
    }

    .white span {
        background-color: white;
    }

</style>


