<template>
    <div>
        <router-link class="column-list" :to="{ path:'details',query:{id: item.id}}" v-for="(item, index) in oplaceData" :key="index" tag="div">
            <div @click="getOplaceData(item.id)">
                <div class="opus">
                    <a href="javascript:;" :title="item.title">
                        <span v-if="item.type === 'mp3'" class="">
                            <img src="static/images_icon/audio_img.png" alt="音频的默认图片" />
                        </span>
                        <span v-else>
                            <img src="" v-lazy="item.detailImg" />
                        </span>
                        <span v-if="item.type === 'mp3' || item.type === 'mp4'" class="play-icon">
                            <img src="static/images_icon/play_icon.png" alt="播放按钮" />
                        </span>
                        <span class="mask"></span>
                        <o-button :text="textObject[item.state]" :classStyle="gatherObject[item.state]" class="gather" @btnClick="changeState"></o-button>
                    </a>
                </div>
                <h3>{{ item.header }}</h3>
                <ul class="information">
                    <li class="author">
                        <a href="javascript:;">
                            <span class="header-img">
                                <img src="" v-lazy="item.authorImg" alt="">
                            </span>
                            <span class="name">{{ item.authorName }}</span>
                        </a>
                    </li>
                    <li class="place">
                        <a href="javascript:;">
                            <span class="place-icon"></span>
                            <span class="name">{{ item.address }}</span>
                        </a>
                    </li>
                    <li class="type">
                        <a href="javascript:;">
                            <span class="type-icon" :class="classObject[item.type]"></span><!-- 
                             --><span class="name">.{{ item.type }}</span>
                        </a>
                    </li>
                </ul>
            </div>
        </router-link>
    </div>
</template>
<script>
    
    import OButton from './button.vue'
    export default {
        data() {
            return {
                classObject: {
                    'jpg': "type-image",
                    'mp4': "type-video",
                    'mp3': "type-audio"
                },
                gatherObject: {
                    '1': "gather-btn",
                    '-1': "gray-btn"
                },
                textObject: {
                    '1': "采集",
                    '-1': "已采集"
                },
                oplace: null, // 存储当前的作品信息
                oplaceComment: [], // 存储当前的作品评论
            }
        },
        computed: {},
        created() {

        },
        methods: {

            // 获取作品信息
            getOplaceData(id) {
                this.oplaceData.forEach(item => {

                    if (item.id === id) {
                        this.oplace = item;
                        return true;
                    }
                });

                // 获取作品的评论信息
                this.comments.forEach(item => {

                    if (item.oplaceId === this.oplace.id) {
                        this.oplaceComment.push(item);
                    }
                })

                // 存储到本地
                localStorage.setItem('oplace', JSON.stringify(this.oplace));
                localStorage.setItem('oplaceComment', JSON.stringify(this.oplaceComment));
            },

            // 改变采集状态
            changeState () {
                // console.log('hihihihi');
            }
        },
        components: {
            OButton
        },
        props: ["oplaceData", "comments"]
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>


    .column-list {
        width: 100%;
        height: 100%;
        overflow: auto;
        padding-bottom: 20px;

        /*作品预览图*/
        .opus {
            position: relative;
            overflow: hidden;
            .play-icon {
                width: 25px;
                height: auto;
                position: absolute;
                top: 50%;
                left: 50%;
                margin-left: -12.5px;
                margin-top: -18px;
                z-index: 0
            }
            .mask {
                background-color: rgba(0, 0, 0, 0.1);
                display: none;
            }
            &:hover {
                .mask, .gather {
                    display: block;
                }
            }
            .gather {
                width: 69px;
                height: 34px;
                position: absolute;
                right: 12px;
                top: 12px;
                // display: none;
            }
        }
        h3 {
            font-size: 13px;
            color: #343434;
            width: 100%;
            height: auto;
            line-height: 18px;
            margin-top: 6px;
            margin-bottom: 10px;
        }
        .information {
            display: block;
            width: 100%;
            height: 18px;
            overflow: hidden;
            li {
                display: block;
                width: auto;
                height: 100%;
                float: left;
                margin-right: 18px;
                span {
                    float: left;
                    margin-right: 5px;
                }
                span.name {
                    width: auto;
                    height: 100%;
                    line-height: 19px;
                    font-size: 12px;
                    color: #626262;
                }
            }
            .author {
                .header-img {
                    display: block;
                    width: 18px;
                    height: 18px;
                    border-radius: 50%;
                    overflow: hidden;
                }
                &:hover {
                    span.name {
                        color: #48abff;
                    }
                }

            }
            .place {
                .place-icon {
                    width: 14px;
                    height: 100%;
                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                    background-position: -81px -283px;
                    background-size: auto 345px;
                }
                &:hover {
                    span.name {
                        color: #48abff;
                    }
                    .place-icon {
                        background-position: -81px -235px;
                    }
                }
            }
            .type {
                .type-icon {
                    width: 18px;
                    height: 100%;
                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                    background-size: auto 280px;
                }
                .type-image {
                    background-position: -121px -264px;
                }
                .type-video {
                    background-position: -167px -30px;
                }
                .type-audio {
                    background-position: -122px -207px;
                }
                &:hover {
                    span.name {
                        color: #48abff;
                    }
                    .type-image {
                        background-position: -122px -237px;
                    }
                    .type-video {
                        background-position: -166px 3px;
                    }
                    .type-audio {
                        background-position: -122px -174px;
                    }
                }
            }
        }
    }
</style>


