<template>
    <div class="wrapper">

        <!-- 数据加载中 -->
        <div class="loading" v-if="loading">
            <div class="ball-box">
                 <div class="loading-ball" v-for="(item, index) in balls" :key="index">{{ item }}</div>
             </div>
        </div>

        <!-- 数据加载成功 -->
        <div class="column" v-else-if="datas">
            <card :oplaceData="datas" :comments="commentsData"></card>
        </div>

        <!-- 加载失败 -->
        <div v-else="error" class="error">
            {{ error }}
        </div>

        <!--发布按钮-->
        <input type="button" readonly="readonly" name="" class="release" @click="showRelease" ref="release" />

        <!--发布弹框-->
        <relase v-show="flag" @closeClick="closePopup"></relase>

        <!--置顶按钮-->
        <go-top-btn></go-top-btn>
    </div>
</template>
<script>
    
    import Card from './card.vue';
    import Relase from '../Popup/Relase/relase.vue';
    import GoTopBtn from './goTop.vue';
    import COMMENT from '../../../comment.js';
    
    export default {
        data() {
            return {
                balls: ["", "", "", "", ""], // 加载动画中的小球
                flag: false, // 上传作品弹窗
                elem: this.$refs.selectBox, // 上传作品 的步骤
                commentsData: [], // 评论数据

            }
        },
        created() {
            this.getAllCommentsData();
        },
        watch: {

        },
        methods: {

            // 显示发布弹窗
            showRelease () {
                this.$refs.release.style.transform = 'rotate(45deg)';
                this.flag = true;
            },

            // 关闭弹窗
            closePopup() {
                this.flag = false;
                this.$refs.release.style.transform = 'rotate(0deg)';
            },

            // 获取评论数据
            getAllCommentsData () {
                this.commentsData = COMMENT.comments;
                console.log(this.commentsData);
                localStorage.setItem('comments', JSON.stringify(this.commentsData));

                // 获取该作品的评论信息
                // this.$axios.get('/api/comment').then(res => {

                //     this.commentsData = res.data.data;
                //     localStorage.setItem('comments', JSON.stringify(this.commentsData));
                // }).catch(err => {
                //     throw new Error("this.$axios.get: " + err);
                // });
            }
        },
        components: {
            Card,
            Relase,
            GoTopBtn
        },
        props: ['loading', 'error', 'datas']
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    
    .wrapper {
        padding: 2px 10px;
    }
    .column {
        -webkit-column-count: 3;
        column-count: 3;
        -webkit-column-gap: 15px;
        column-gap: 15px;
    }

    /*发布按钮*/
    .release {
        position: fixed;
        bottom: 180px;
        right: 60px;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        background: url(../../../static/images_icon/sprite.png) no-repeat #48ABFF;
        background-position: -222px -243px;
        background-size: auto 300px;
        box-shadow: 0 2px 10px rgba(0, 138, 255, 0.5);
        z-index: 2;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
        &:hover {
            box-shadow: 0 0px 15px rgba(0, 137, 255, 0.8);
        }
    }
</style>



