<template>
    <popup>
        <div slot="content" class="release-popup">
            <!-- 弹框标题 -->
            <h2 class="header">
                <span class="edit-icon"></span>
                <p class="text">上传作品</p>
            </h2>

            <!-- 弹框内容 -->
            <div class="container-wrapper" ref="selectBox">

                <!-- 第一部分：上传图片/视频/音频 -->
                <div class="slide-box upload-box">
                    <div class="upload-frame">
                        <a href="">
                            <input type="file" class="upload-input">
                            <p class="upload-frame-tips">上传图片、视频、音频文件</p>
                        </a>
                    </div>
                    <textarea placeholder="请输入作品描述" class="upload-describe"></textarea>
                    <div class="alert-btn-box">
                        <o-button :text="'下一步'" :classStyle="'main-btn'" class="next-btn" @btnClick="nextStep"></o-button>
                    </div>
                </div>

                <!-- 第二部分：选择地点/作品标签/作品价格 -->
                <div class="slide-box select-box">

                    <!-- 地图 -->
                    <div class="upload-map-box">
                        <input type="text" placeholder="搜索精确地点" class="map-search">
                        <input type="button" class="map-search-btn">
                        <h2 class="map-search-title">选择拍摄地点</h2>
                        <span class="location"></span>
                        <div class="map-location-tips-box">
                            <h3 class="map-location-tips">朝阳公园</h3>
                        </div>
                    </div>

                    <!-- 作品标签 -->
                    <div class="label-box opus-label">
                        <div class="input-box">
                            <h3 class="label-title">作品标签：</h3>
                            <input type="text" placeholder="请输入作品标签" class="input-control add-label-input">
                            <o-button :text="'添加'" :classStyle="'main-btn'" class="add-label-btn" @btnClick=""></o-button>
                        </div>
                        <ul class="labels-list">
                            <li class="item" v-for="(value, index) in labels" :key="index">{{ value }}</li>
                        </ul>
                        <h3 class="com-labels-title">常用标签：</h3>
                        <ul class="labels-list">
                            <li class="item" v-for="(value, index) in comLabels" :key="index">{{ value }}</li>
                        </ul>
                    </div>
                    <div class="label-box opus-price">
                        <span class="input-box">
                            <h3 class="label-title">作品价格：</h3>
                            <input type="number" placeholder="" class="input-control opus-price-input"><span class="explain-text">元</span>
                        </span>
                        <p class="transaction-price">以此价格成交，大约收益为：<strong>380</strong>元</p>
                        <p class="transaction-explain">作品成功售出后，平台将抽取一部分佣金，详细说明请参见《<a href="">原点作者使用协议</a>》。</p>
                    </div>
                    <div class="buttons">
                        <o-button :text="'上一步'" :classStyle="'main-btn'" class="btn" @btnClick="returnStep"></o-button>
                        <o-button :text="'发 布'" :classStyle="'main-btn'" class="btn" @btnClick="relaseStep"></o-button>
                    </div>
                </div>

                <!-- 第三部分：上传结果 -->
                <div class="slide-box">

                    <!-- 上传中 -->
                    <div class="loading" v-if="false">
                        <div class="ball-box">
                             <div class="loading-ball" v-for="(item, index) in balls" :key="index">{{ item }}</div>
                         </div>
                    </div>

                    <!-- 上传成功 -->
                    <div class="upload-result" v-if="false">
                        <div class="uploading">
                            <span class="upload-img success-img"></span>
                            <p class="upload-text">上传成功</p>
                        </div>
                        <o-button :text="'查看作品'" :classStyle="'main-btn'" class="btn" @btnClick=""></o-button>
                    </div>

                    <!-- 上传失败 -->
                    <div class="upload-result" v-if="true">
                        <div class="uploading">
                            <span class="upload-img error-img"></span>
                            <p class="upload-text">上传失败</p>
                        </div>
                        <o-button :text="'重新上传'" :classStyle="'main-btn'" class="btn" @btnClick="ReUpload"></o-button>
                    </div>
                </div>
            </div>
        </div>
        <input type="button" name="" class="close-btn" slot="close" @click="closePopup" />
    </popup>
</template>

<script>
    
    import OButton from '../../component/button.vue';
    import Popup from '../../component/popup.vue';
    
    export default {
        data() {
            return {
                labels: ["风景阿姆", "山水中国", "山川河流", "风景"],
                comLabels: ["人物", "中国", "河流", "风景"],
                elem: this.$refs.selectBox, // 上传作品 的步骤
                balls: ["", "", "", "", ""], // 加载动画中的小球
            }
        },
        methods: {

            // 关闭弹窗
            closePopup() {

                // 执行上传图标的旋转动画
                this.$emit('closeClick');
            },

            // 下一步
            nextStep () {
                this.changeStep(this.$refs.selectBox, "back", "next", 520);
            },

            // 返回上一步
            returnStep () {
                this.changeStep(this.$refs.selectBox, "next", "back", 397);
            },

            // 发布作品
            relaseStep () {
                this.changeStep(this.$refs.selectBox, "next", "last", 397);
            },

            // 点击重新上传
            ReUpload () {
                this.changeStep(this.$refs.selectBox, "last", "back", 397);
            },

            // 上传作品 的步骤
            changeStep(elem, removeClass, addClass, height) {
                elem.classList.remove(removeClass);
                elem.classList.add(addClass);
                elem.style.height = height + 'px';
            }
        },
        components: {
            OButton,
            Popup,
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    
    /*发布弹框*/
    .release-popup {
        overflow: hidden;
        width: 500px;
        .header {
            height: 43px;
            padding: {
                top: 18px;
                left: 20px;
            }
            .edit-icon {
                width: 20px;
                height: 20px;
                margin-top: 2px;
                float: left;
                background: url(/static/images_icon/sprite.png) center center no-repeat;
                background-position: -181px -114px;
                background-size: auto 300px;
            }
            .text {
                float: left;
                margin-left: 10px;
                font: {
                    size: 18px;
                    weight: bold;
                }
            }
        }
        .next {
            margin-left: -100%;
        }
        .last {
            margin-left: -200%;
        }
        .back {
            margin-left: 0;
        }

        .container-wrapper {
            width: 300%;
            height: 397px;
            margin-top: 22px;
            max-height: 85vh;
            min-height: 397px;
            overflow: hidden;
            transition: all 0.3s cubic-bezier(.07, .63, .39, .98);
            .slide-box {
                width: 33.33%;
                float: left;
                padding: 0 30px;
                overflow-y: auto;
                max-height: 100%;
            }

            // 第一部分：上传图片/视频/音频
            .upload-box {
                .upload-frame {
                    width: 100%;
                    height: 210px;
                    border-radius: 5px;
                    margin: 0 auto;
                    background: url(/static/images_icon/add.png) center 72px no-repeat #f4f4f4;
                    background-size: 35px auto;
                    position: relative;
                    overflow: hidden;
                    .upload-input {
                        display: block;
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        cursor: pointer;
                        opacity: 0;
                    }
                    .upload-frame-tips {
                        font-size: 14px;
                        color: #C7C7C7;
                        text-align: center;
                        font-weight: bold;
                        padding-top: 120px;
                    }
                }
                .upload-describe {
                    display: block;
                    resize: none;
                    width: 100%;
                    height: 78px;
                    border-radius: 5px;
                    background-color: #F4F4F4;
                    margin: 0 auto;
                    margin-top: 15px;
                    padding: 10px;
                    font-size: 14px;
                    font-weight: bold;
                    overflow: hidden;
                    color: #343434;
                }
                .next-btn {
                    display: flex;
                    justify-content: center;
                    overflow: hidden;
                    margin: 30px auto;
                    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.10);
                    width: 78px;
                    height: 34px;
                }
            }

            // 第二部分：选择地点/作品标签/作品价格
            .select-box {

                // 地图
                .upload-map-box {
                    width: 100%;
                    height: 200px;
                    background: #ddd;
                    border-radius: 5px;
                    position: relative;
                    overflow: hidden;
                }
                .baidu-map {
                    width: 100%;
                    height: 200px;
                }

                .map-search {
                    display: block;
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    width: 140px;
                    height: 28px;
                    line-height: 28px;
                    border-radius: 200px;
                    z-index: 1;
                    padding: {
                        left: 10px;
                        right: 28px;
                    }
                }

                .map-search-btn {
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    background: url(/static/images_icon/sprite.png) no-repeat;
                    background: {
                        position: -68px -36px;
                        size: auto 180px;
                    }
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    z-index: 10;
                }

                .map-search-btn:active {
                    background-color: #F0F0F0 !important;
                }

                .map-search-title {
                    width: 84px;
                    height: 25px;
                    line-height: 25px;
                    color: #48ABFF;
                    font: {
                        size: x-small;
                        weight: normal;
                    }
                    position: absolute;
                    left: 0;
                    top: 0;
                    z-index: 11;
                    text-align: center;
                    background-color: #fff;
                }

                .location {
                    display: block;
                    width: 22px;
                    height: 22px;
                    background: url(/static/images_icon/sprite.png) no-repeat #ff0;
                    background: {
                        position: -65px -201px;
                        size: auto 300px;
                    }
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    z-index: 11;
                }

                .map-location-tips-box {
                    position: absolute;
                    top: 50%;
                    margin-top: -39px;
                    width: 100%;
                    height: 20px;
                    z-index: 11;
                    display: table;
                    text-align: center;
                }

                .map-location-tips {
                    margin: 0 auto;
                    display: inline-block;
                    background-color: #48ABFF;
                    width: auto;
                    height: 100%;
                    line-height: 20px;
                    padding: 2px 6px;
                    border-radius: 200px;
                    color: #fff;
                    font-size: 12px;
                    font-weight: normal;
                }

                // 作品标签
                .label-box {
                    width: 94%;
                    height: auto;
                    margin: 0 auto;
                    padding-top: 20px;
                    overflow: hidden;
                    .input-box {
                        display: block;
                        width: 100%;
                        height: 32px;
                        overflow: hidden;
                        .label-title {
                            float: left;
                            font-size: 13px;
                            line-height: 32px;
                        }
                        .input-control {
                            display: block;
                            width: 225px;
                            height: 100%;
                            border-radius: 4px;
                            float: left;
                            background-color: #F4F4F4;
                            padding: 0 6px;
                            font-size: 13px;
                        }
                        .add-label-btn {
                            width: 58px;
                            height: 100%;
                            float: left;
                            font-size: 13px;
                            margin-left: 6px;
                        }
                    }
                }

                .opus-label {
                    .labels-list {
                        padding-left: 65px;
                        margin-top: 14px;
                        overflow: hidden;
                        .item {
                            font-size: smaller;
                            float: left;
                            line-height: 20px;
                            background-color: #E8E8E8;
                            border-radius: 2px;
                            margin: {
                                right: 10px;
                                bottom: 10px;
                            }
                            padding: 0 8px;
                        }
                    }
                    .com-labels-title {
                        font-size: 12px;
                        color: #626262;
                        font-weight: normal;
                        width: 100%;
                        padding-left: 65px;
                        margin-top: 10px;
                    }
                    .add-label-input {
                        width: 128px !important;
                    }
                }
                .opus-price {
                    .opus-price-input {
                        width: 78px !important;
                    }
                    .transaction-price {
                        font-size: smaller;
                        font-weight: bold;
                        padding-left: 65px;
                        margin-top: 10px;
                        line-height: 14px;

                    }
                    .transaction-explain {
                        font-size: smaller;
                        padding-left: 65px;
                        margin-top: 3px;
                        line-height: 18px;
                        > a {
                            display: inline;
                            line-height: 18px;
                            text-decoration: underline;
                        }
                    }
                    .explain-text {
                        height: 100%;
                        float: left;
                        padding: 0 8px;
                        line-height: 36px;
                        font-size: 13px;
                    }
                }
                .buttons {
                    width: 182px;
                    margin: 0 auto;
                    .btn {
                        margin: 20px 5px;
                        width: 78px;
                        height: 34px;
                        float: left;
                    }
                }

            }

            // 第三部分：上传作品结果
            .upload-result {
                width: 100px;
                margin: 0 auto;
                text-align: center;
                .uploading {
                    height: 320px;
                    padding-top: 100px;
                    .upload-img {
                        width: 44px;
                        height: 44px;
                        margin: 0 auto;
                        border-radius: 50%;
                        background: url(/static/images_icon/sprite.png) center center no-repeat #fe6448;
                        background-size: auto 400px;
                    }
                    .error-img {
                        background-position: -165px -193px;
                    }
                    .success-img {
                        background-position: -165px -240px;
                    }
                    .upload-text {
                        font: {
                            size: smaller;
                            weight: bold;
                        }
                        line-height: 3;
                        color: #6d6969;
                    }
                }
                .btn {
                    width: 78px;
                    height: 34px;
                    margin-left: 11px;
                }
            }
        }
    }
</style>


