<template>
    <input :type="inputType"
           :value="text"
           :class="classStyle"
           @click="inputClick"
           :disabled="classStyle==='disabled-btn'"
           name="" />
</template>
<script>
    export default {
        data() {
            return {}
        },
        props: {
            inputType: {
                type: [String],
                default: "button"
            },
            text: {
                type: [String, Number],
                default: "安静的按钮"
            },
            classStyle: {
                type: [String],
                default: "defalut-btn"
            }
        },
        methods: {
            inputClick() {
                this.$emit("btnClick");
            }
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

    input[type='button'], input[type='submit'] {
        border-radius: 3px;
        z-index: 2;
        font-size: 14px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
    }

    // 灰色按钮
    .gray-btn {
        color: #a8a8a8;
        background: #fbfbfb;
        border: 1px solid #e2e2e2;
        &:hover {
          background: #fff;
        }
    }

    // 默认按钮
    .default-btn {
        &:hover {
          background: #fff;
        }
        background: #fff;
        color: #a8a8a8;
    }

    // 已采集
    .gather-btn {
        background-image: -webkit-linear-gradient(top, #50bfc3, #00C9D1);
        background-image: linear-gradient(180deg,#50bfc3,#00C9D1);
        background-color: #00C9D1;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
        color: #E0FFFE;
    }

    // 主色按钮
    .main-btn {
        color: #e5f5ff;
        background: #48ABFF;
        &:hover {
            background: #319cf7;
            background-image: -webkit-linear-gradient(top, #48ABFF, #319cf7);
            background-image: linear-gradient(180deg,#48ABFF,#319cf7);
        }
    } 

    // 删除按钮
    .del-btn {
        background: #fe6448;
        color: #fff;
        &:hover {
            background: #ef4122;
        }
    }   

    // 禁用
    .disabled-btn {
        color: #fff;
        background: #D6D6D6;
        cursor: not-allowed;
    }
</style>


